<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogAddRole = true"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <!-- 角色列表 -->
      <el-table :data="roleData" style="width: 100%" border stripe>
        <el-table-column
          type="index"
          width="80"
          align="center"
          label="用户编号"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="roleName"
          label="角色名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="createTime"
          label="创建时间"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="remark"
          label="角色说明"
        ></el-table-column>
        <el-table-column align="center" label="操作" width="120px">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="test(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  
              v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
              ></el-button>
            </el-tooltip>

            
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="添加角色"
        :visible.sync="dialogAddRole"
        width="50%"
        @close="closeAddRole"
      >
        <el-form ref="form" v-model="addRoleForm" label-width="80px">
          <el-form-item label="角色名称">
            <el-input v-model="addRoleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述">
            <el-input type="textarea" v-model="addRoleForm.remark"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addSave">立即创建</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      //控制按钮权限
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      roleData: [],
      dialogAddRole: false,
      addRoleForm: {}
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      console.log(arr);
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    async queryPage() {
      const { data: res } = await this.$http.get('/jxyxSysRole/queryPage')
      console.log(res)
      if (res.code != 20000) return this.$message.error(res.message)
      this.roleData = res.data.list
    },
    test(id) {
      this.$router.push({ path: '/resume', query: { jId: id } })
    },
    closeAddRole() {},
    async addSave() {
      const { data: res } = await this.$http.post(
        '/jxyxSysRole/save',
        this.addRoleForm
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.dialogAddRole = false
      this.queryPage()
      return this.$message.success(res.message)
    },
    addDown() {}
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
</style>
